// Screen =======================================================================
// Width
$width-max: 1280px;
$width-articles-max: 720px;
// Break Points
$breakpoints: (
  sm: "(min-width: 640px)",
  md: "(min-width: 768px)",
  lg: "(min-width: 1024px)",
  xl: "(min-width: 1280px)",
) !default;

// Font ========================================================================
// Font Family
$font-base: -apple-system,               //macOS,iOS 適用フォントはSan Francisco(iOS11以前は少し違う)
            BlinkMacSystemFont,          //macOSのChromeとOpera 適用フォントはSan Francisco
            "Segoe UI",                  //Windows
            Roboto,                      //Android
            "Hiragino Kaku Gothic ProN", //日本語フォント macOS,iOS(Hiragino Sansより細く、bold時の黒潰れの無さが好み)
            "Yu Gothic",                 //日本語フォント Windows
            sans-serif,                  //全OS共通
            "Apple Color Emoji",
            "Segoe UI Emoji",
            "Segoe UI Symbol",
            "Noto Sans Emoji";

// Font Size
$font-size-base: 1.6rem;
$font-size-subtitle: 4.8rem;
$font-size-xs: 1rem;
$font-size-sm: 1.4rem;
$font-size-md: 1.8rem;
$font-size-lg: 2rem;
$font-size-xl: 2.4rem;

// Font Weight
$font-weight-thin: 100;
$font-weight-regular: 400;
$font-weight-bold: 700;

// Google Fonts
@import url('https://fonts.googleapis.com/css2?family=Josefin+Slab:wght@100&family=Wallpoet&display=swap');
$font-title: 'Wallpoet', serif;
$font-section_title: 'Josefin Slab', serif;

// Google Fonts-Icons
/* サイズ */
.material-icons-round.md-18 { font-size: 18px; }
.material-icons-round.md-24 { font-size: 24px; }
.material-icons-round.md-36 { font-size: 36px; }
.material-icons-round.md-48 { font-size: 48px; }

/* 通常 */
.material-icons-round.md-dark { color: rgba(0, 0, 0, 0.54); }
.material-icons-round.md-dark.md-inactive { color: rgba(0, 0, 0, 0.26); }

/* ダークモード */
.material-icons-round.md-light { color: rgba(255, 255, 255, 1); }
.material-icons-round.md-light.md-inactive { color: rgba(255, 255, 255, 0.3); }

// Color =======================================================================
// Font Color
$color-text-black:       #222222;
$color-text-black-light: #6E6E6E;
$color-text-white:       #f5f5f5;
// $color-text-white-dark:  #bcbcbc;

// Background Color
$color-background-base:        #fcfcfc;
$color-background-black:       #757575;
$color-background-black-light: #656565;
$color-background-white:       #fff;

// Border
$color-border-base: #707070;
$color-border-thin: #dbdbdb;

// Primary Color
$color-primary: #abf5d9;
$color-primary-dark: #7ac2a8;
$color-primary-light: #deffff;

// Secondary Color
$color-secondary: #ff80ab;
$color-secondary-dark: #c94f7c;
$color-secondary-light: #ffb2dd;

// Footer
$color-footer-text-thin:  #515154;
$color-footer-background: #f5f5f7;

// Link
$color-link:         #0000EE;
$color-link-visited: #551A8B;

// Ad
$color-ad: #FBC02D;

// SNS
$color-sns-twitter: #1DA1F2;
// $color-sns-line: ;

// Space =======================================================================
$spacing-4:     4px;
$spacing-8:     8px;
$spacing-16:   16px;
$spacing-24:   24px;
$spacing-32:   32px;
$spacing-48:   48px;
$spacing-64:   64px;
$spacing-88:   88px;
$spacing-96:   96px;
$spacing-128: 128px;
$spacing-192: 192px;
$spacing-256: 256px;

// Round =======================================================================
$round-4:   4px;
$round-8:   8px;
$round-16: 16px;

// BoxShadow ===================================================================
%shadow-z0 {
  box-shadow: 0 0 0 0 rgb(0 0 0 / 20%),
              0 0 0 0 rgb(0 0 0 / 14%),
              0 0 0 0 rgb(0 0 0 / 12%);
}
%shadow-z1{
  box-shadow: 0 2px 1px -1px rgb(0 0 0 / 20%),
              0 1px 1px 0 rgb(0 0 0 / 14%),
              0 1px 3px 0 rgb(0 0 0 / 12%);
}
%shadow-z2{
