@use "../global/global" as g;

.l-blog-container{
  width: 100%;
  padding: g.$spacing-8;
  @include g.media-queries("md"){
    padding: g.$spacing-16;
  }
}
.l-blog-article_card-list{
  width: 100%;
  margin: 0 auto;
  display: grid;
  justify-content: center;
  grid-column-gap: g.$spacing-64;
  grid-row-gap: g.$spacing-48;
  @include g.media-queries("sm"){
    grid-template-columns: minmax(auto, 640px);
  }
  @include g.media-queries("md"){
    grid-template-columns: repeat(2, minmax(auto, 320px));
  }
  @include g.media-queries("lg"){
    grid-template-columns: repeat(3, minmax(auto, 320px));
  }
}
