@use "../global/global" as g;

.c-category_menu{
  width: 100%;
  margin: 0 auto;
  padding: 12px 4px 8px 4px;
  background: g.$color-background-base;
  display: flex;
  overflow: scroll;
  z-index: 999;
  @extend %shadow-z16;
  position: fixed;
  left: 0;
  bottom: 0;
  @include g.media-queries("md"){
    max-width: calc((320px * 3 + 64px * 2) + 32px); // カード, gap, paddingから算出
    padding: 12px 16px 12px 16px;
    margin-bottom: 16px;
    grid-column-gap: 8px;
    overflow: auto;
    box-shadow: none;
    position: static;
  }
  .c-button{
    font-size: g.$font-size-sm;
    color: g.$color-text-black-light;
    flex-shrink: 0;
    @include g.media-queries("md"){
      border: 1px solid g.$color-border-thin;
      border-radius: 25px;
      padding: 4px 16px;
    }
  }
  .c-button.js-current_page{
    color: g.$color-text-black;
    position: relative;
    @include g.media-queries("md"){
      color: g.$color-text-white;
      background: g.$color-background-black;
      border: 1px solid g.$color-background-black;
    }
    &:before{
      content: "";
      height: 3px;
      width: 100%;
      background: g.$color-border-base;
      border-radius: g.$round-16;
      position: absolute;
      top: -11px;
      left: 0;
      @include g.media-queries("md"){
        display: none;
      }
    }
  }
}
