@use "../global/global" as g;

.l-header{
  width: 100%;
  padding: 0 g.$spacing-8;
  margin-bottom: g.$spacing-48;
  @extend %shadow-z4;
  @include g.media-queries("md"){
    padding: 0 16px;
  }
}
.l-header-inner{
  width: 100%;
  max-width: g.$width-max;
  margin: 0 auto;
  display: flex;
}
.l-header-navigation{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.l-header-title{
  display: flex;
  align-items: center;
  #svg-main_icon{
    height: 48px;
    width: 48px;
    @include g.media-queries("md"){
      height: 64px;
      width: 64px;
    }
  }
}
.l-header-global_nav{
  font-family: g.$font-section_title;
  font-size: g.$font-size-lg;
  align-self: flex-end;
  display: none;
  grid-column-gap: g.$spacing-16;
  @include g.media-queries("md"){
    display: flex;
  }
  >li{
    padding: 4px;
    color: g.$color-text-black-light;
  }
  >li.js-current_page{
    color: g.$color-text-black;
    font-weight: g.$font-weight-bold;
    position: relative;
    &:after{
      content: "";
      height: 3px;
      width: 100%;
      background: g.$color-border-base;
      border-radius: g.$round-16;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}
.l-header-section_title-wrap{
  @include g.media-queries("md"){
    display: none;
  }
  .l-header-section_title{
    font-family: g.$font-section_title;
    font-size: g.$font-size-subtitle;
    font-weight: g.$font-weight-thin;
    line-height: 1.3;
  }
}
