@use "../global/global" as g;

.p-pagination-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column-gap: g.$spacing-32;
  margin: g.$spacing-48;
  @include g.media-queries("md"){
    margin: 96px 0;
  }
  .previous_button, .next_button{
    height: 3.2rem;
    width: 3.2rem;
    border: 1px solid g.$color-border-base;
    border-radius: 100%;
    position: relative;
    .material-icons-round{
      font-size: 2.4rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-48%, -50%);
    }
  }
  .previous_button{
    transform: rotate(180deg);
  }
  .page_numbers{
    font-family: "Segoe UI",
                 Roboto,
                 "Hiragino Kaku Gothic ProN",
                 "Yu Gothic",
                 sans-serif;
    position: relative;
    .page_numbers-button{
      line-height: 1;
      padding: 8px 48px;
      border: 1px solid g.$color-border-base;
      border-radius: 16px;
      position: relative;
      .material-icons-round{
        font-size: 28px;
        position: absolute;
        top: 52%;
        right: 3px;
        transform: translateY(-50%) rotate(90deg);
      }
    }
    .page_numbers-list{
      height: 0; 
      overflow: hidden;
      width: 100%;
      padding: g.$spacing-8 0;
      border: 1px solid g.$color-border-base;
      border-radius: 0 0 16px 16px;
      display: flex;
      flex-direction: column;
      align-items: center;
      grid-row-gap: 8px;
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
