@use "../global/global" as g;

html {
  font-size: 10px;
}
body{
  width: 100%;
  color: g.$color-text-black;
  font-size: g.$font-size-base;
  font-weight: g.$font-weight-regular;
  line-height: 1.6;
  letter-spacing: .03em;
  font-family: g.$font-base;
  background: g.$color-background-base;
  position: relative;
  font-feature-settings: "palt"; // プロポーショナルフォントの適用
}
h2,
h3,
h4,
h5,
h6 {
  color: g.$color-text-black;
}
pre{
  font-family: inherit;
  white-space: pre-wrap;
}
strong{
  font-weight: g.$font-weight-bold;
  color: g.$color-text-black;
}
a{
  color: g.$color-text-black;
}
img{
  width: 100%;
  height: auto;
}
