.l-header {
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
}

.l-header {
  width: 100%;
  padding: 0 8px;
  margin-bottom: 48px;
}
@media screen and (min-width: 768px) {
  .l-header {
    padding: 0 16px;
  }
}

.l-header-inner {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
}

.l-header-navigation {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.l-header-title {
  display: flex;
  align-items: center;
}
.l-header-title #svg-main_icon {
  height: 48px;
  width: 48px;
}
@media screen and (min-width: 768px) {
  .l-header-title #svg-main_icon {
    height: 64px;
    width: 64px;
  }
}

.l-header-global_nav {
  font-family: "Josefin Slab", serif;
  font-size: 2rem;
  align-self: flex-end;
  display: none;
  grid-column-gap: 16px;
}
@media screen and (min-width: 768px) {
  .l-header-global_nav {
    display: flex;
  }
}
.l-header-global_nav > li {
  padding: 4px;
  color: #6E6E6E;
}
.l-header-global_nav > li.js-current_page {
  color: #222222;
  font-weight: 700;
  position: relative;
}
.l-header-global_nav > li.js-current_page:after {
  content: "";
  height: 3px;
  width: 100%;
  background: #707070;
  border-radius: 16px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .l-header-section_title-wrap {
    display: none;
  }
}
.l-header-section_title-wrap .l-header-section_title {
  font-family: "Josefin Slab", serif;
  font-size: 4.8rem;
  font-weight: 100;
  line-height: 1.3;
}
