.c-category_menu {
  box-shadow: 0 8px 10px -5px rgba(0, 0, 0, 0.2), 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12);
}

.c-category_menu {
  width: 100%;
  margin: 0 auto;
  padding: 12px 4px 8px 4px;
  background: #fcfcfc;
  display: flex;
  overflow: scroll;
  z-index: 999;
  position: fixed;
  left: 0;
  bottom: 0;
}
@media screen and (min-width: 768px) {
  .c-category_menu {
    max-width: calc((320px * 3 + 64px * 2) + 32px);
    padding: 12px 16px 12px 16px;
    margin-bottom: 16px;
    grid-column-gap: 8px;
    overflow: auto;
    box-shadow: none;
    position: static;
  }
}
.c-category_menu .c-button {
  font-size: 1.4rem;
  color: #6E6E6E;
  flex-shrink: 0;
}
@media screen and (min-width: 768px) {
  .c-category_menu .c-button {
    border: 1px solid #dbdbdb;
    border-radius: 25px;
    padding: 4px 16px;
  }
}
.c-category_menu .c-button.js-current_page {
  color: #222222;
  position: relative;
}
@media screen and (min-width: 768px) {
  .c-category_menu .c-button.js-current_page {
    color: #f5f5f5;
    background: #757575;
    border: 1px solid #757575;
  }
}
.c-category_menu .c-button.js-current_page:before {
  content: "";
  height: 3px;
  width: 100%;
  background: #707070;
  border-radius: 16px;
  position: absolute;
  top: -11px;
  left: 0;
}
@media screen and (min-width: 768px) {
  .c-category_menu .c-button.js-current_page:before {
    display: none;
  }
}
