@use "../global/global" as g;

.p-article_card{
  border: 1px solid g.$color-border-thin;
  border-radius: g.$round-4;
  img{
    border-radius: g.$round-4 g.$round-4 0 0;
  }
  .p-article_card-content{
    padding: g.$spacing-16 g.$spacing-16 g.$spacing-8 g.$spacing-16;
  }
  .p-article_card-title{ 
    font-size: g.$font-size-md;
    font-weight: g.$font-weight-bold;
    padding-bottom: g.$spacing-8;
    border-bottom: 1px solid g.$color-border-thin;
  }
  .p-article_card-date{
    font-size: g.$font-size-xs;
    padding-top: g.$spacing-8;
    @extend %flex-center;
  }
}
